.avatar {
    width: 6rem;
    height: 6rem;
    max-width: 6rem;
    max-height: 6rem;
    border-radius: 50rem;
}

header > a:hover {
  opacity: 0.8;
}

.name {
    font-size: 3rem;
    margin: 0;
}

.title {
    font-size: 1.5rem;
    margin: 0;
}

.name-title {
    display: inline-block;
    margin-left: 1rem;
}

.header-item {
    display: inline;
    margin: 0 1rem;
}

.header {
    position: sticky;
    top: 0;
    width: 100%;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
}